<template>
	<div>
		<h2>Google Charts</h2>
		<h3>점심 메뉴 투표</h3>
		<GChart
			type="PieChart"
			:data="chartData"
			:options="chartOptions"
			style="width: 100%; height: 500px"
		/>
	</div>
	<div id="app">
		<transition-group tag="ul" name="fade">
			<li v-for="(item, c) in filteredChartData" :key="c">
				{{ item[0] }} : {{ item[1] }}
				<button v-on:click="addOne(c)">한표</button>
			</li>
		</transition-group>
		<label>
			<input v-model="newItemName" placeholder="추가할 메뉴 이름" />
		</label>
		<button v-on:click="addList">추가</button>
		<p>
			<button v-on:click="removeLast">맨뒤 1개 삭제</button>
		</p>
	</div>
</template>

<script>
import { GChart } from "vue-google-charts";

export default {
	name: "App",
	components: {
		GChart,
	},
	data() {
		return {
			chartData: [["종류", "개수"]],
			newItemName: "",
			newItemValue: 1,
			chartOptions: {
				title: "선호하는 점심 메뉴",
				is3D: true,
			},
		};
	},
	computed: {
		filteredChartData() {
			// 첫 번째 항목을 제외한 데이터만 반환
			return this.chartData.slice(1);
		},
	},
	methods: {
		addOne(val) {
			// val 인덱스가 1부터 시작되므로, 1을 더한 인덱스로 접근
			var obj = this.chartData[val + 1];
			obj[1]++;
			this.chartData.splice(val + 1, 1, obj);
		},
		addList() {
			if (this.newItemName && this.newItemValue >= 0) {
				this.chartData.push([this.newItemName, this.newItemValue]);
				this.newItemName = "";
				this.newItemValue = 1;
			}
		},
		removeLast() {
			if (this.chartData.length > 1) {
				this.chartData.pop();
			}
		},
	},
};
</script>

